 export default function anchor(lastLocation, currentLocation, active, callback) {//  ,   , ,    
    let once = 1; //       ....       
    if (active) { //    
        window.location.href = currentLocation;

    }
    window.addEventListener('hashchange', (e) => { // url 

        if (window.location.href[window.location.href.length - 1] == lastLocation[lastLocation.length-1] && active && once == 1) { //    url     url....
            callback();
            once = 0;
            console.log('   ')
        }
    })
}